.cloned_table {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    z-index: 10;

    th, td {

        &:not(.sticky-column) {
            visibility: hidden;
            border-color: transparent !important;
        }

        &.sticky-column {
            border-right-width: 1px;
        }
    }

    .bg-blue {
        th, td {
            &.sticky-column {
                border-right-width: 0px;
            }
        }
    }
}

